<template>
	<div class="about-container">
		<div class="about-header">
			<div class="about-zhong">
				<div class="about-quxiao">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="about-ti">关于我们</div>
				<div></div>
			</div>
		</div>
		<div class="about-content">
			<div class="about-juzhong">
				<div class="about-logo">
					<!--<img src="../assets/icon_logo.png">-->
					<img :src="!!data.correlation_pic?baseUrl+data.correlation_pic:'../assets/icon_logo.png'">
				</div>
			</div>
			<div class="about-juzhong">
				<div class="about-huashu">{{data.correlation_appname}}</div>
			</div>
			<div class="about-juzhong">
				<div class="about-shuzi">{{data.correlation_version}}</div>
			</div>
		</div>
		<div class="about-footer">

		</div>
	</div>
</template>

<script>
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				data: {}
			}
		},
		methods: {
			back(){
				window.history.back();
			},
			info_correlation() {
				var that = this;
				that.$http.post('/help/info_correlation', {}).then((res) => {
					if(res.meta.code == 200) {
					that.data = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			this.info_correlation();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.about-container {
		width: 100%;
		height: 100%;
	/*	background: #FFFFFF;*/
	}
	
	.about-header {
		height: 50px;
		/*background: #FFFFFF;*/
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.about-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.about-quxiao img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.about-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
		font-weight: 550;
	}
	
	.about-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	.about-juzhong{
		display: flex;
		justify-content: center;
	}
	.about-logo img{
		width: auto;
		height: 5rem;
		margin-top: 1.5rem;
	}
	.about-huashu{
		font-size: 1.6rem;
		padding-top: 0.8rem;
		font-weight: 550;
	}
	.about-shuzi{
		font-size: 1.4rem;
		color: #8a8a8a;
		padding-top: 0.5rem;
	}
</style>


